@media screen and (max-width: 768px) {
  .nav-container .nav-scroll {
    margin: 0 -5% -20px;
  }
}
.nav-container .nav-scroll .nav-inner {
  border-bottom: 2px solid #ECECEC;
  display: inline-block;
  font-size: 0;
  margin-bottom: 20px;
  min-width: 100%;
  padding-top: 5px;
  vertical-align: middle;
}
.nav-container .nav-scroll .nav-inner .nav-item {
  display: inline-block;
  font-size: 0;
}
.nav-container .nav-scroll .nav-inner .nav-item a {
  color: #401019;
  display: block;
  font-size: 16px;
  font-weight: 300;
  padding: 10px;
  white-space: nowrap;
  position: relative;
}
.nav-container .nav-scroll .nav-inner .nav-item a:after {
  content: "";
  background: #7d1f31;
  height: 2px;
  width: 100%;
  left: 0px;
  position: absolute;
  bottom: -1px;
  transition: all 250ms ease 0s;
  transform: scale(0);
}
.nav-container .nav-scroll .nav-inner .nav-item.active a,
.nav-container .nav-scroll .nav-inner .nav-item a:hover,
.nav-container .nav-scroll .nav-inner .nav-item a:focus {
  color: #7d1f31;
  text-decoration: none;
  transition: all 250ms ease 0s;
}
.nav-container .nav-scroll .nav-inner .nav-item.active a:after,
.nav-container .nav-scroll .nav-inner .nav-item a:hover:after,
.nav-container .nav-scroll .nav-inner .nav-item a:focus:after {
  transform: scale(1);
  transition: all 250ms ease 0s;
}
.nav-container .nav-arrow-right,
.nav-container .nav-arrow-left {
  background-color: white;
  bottom: 12px;
  color: #d6d6d6;
  font-size: 33px;
  line-height: 1.3;
  min-width: 50px;
  position: absolute;
  top: 0;
  vertical-align: middle;
  width: 5%;
  z-index: 999;
}
.nav-container .nav-arrow-right:hover,
.nav-container .nav-arrow-left:hover {
  text-decoration: none;
}
.nav-container .nav-arrow-right {
  right: 3%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #ffffff 47%, #ffffff 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  padding-right: 5px;
  text-align: right;
}
@media screen and (max-width: 768px) {
  .nav-container .nav-arrow-right {
    right: 0;
  }
}
.nav-container .nav-arrow-left {
  left: 3%;
  background: linear-gradient(to left, rgba(255, 255, 255, 0) 0%, #ffffff 47%, #ffffff 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  padding-left: 5px;
  text-align: left;
}
@media screen and (max-width: 768px) {
  .nav-container .nav-arrow-left {
    left: 0;
  }
}
